<template>
  <div class="bidding">
    <Modal
      v-model="isPop"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled"></span>
        <span v-else></span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label=""
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="ruleValidateType"
            :label-width="85"
          >
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label=""
                prop="organizational_form"
              >
                <Select
                  v-model="form.organizational_form"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder=""
                >
                  <Option v-for="(item,key) in organizationalForm" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="person_charge"
              >
                <Input
                  v-model.trim="form.person_charge"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label=""
                prop="phone"
              >
                <Input
                  v-model.trim="form.phone"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="code"
              >
                <Input
                  v-model.trim="form.code"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label=""
                prop="bidding_name"
              >
                <Input
                  v-model.trim="form.bidding_name"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="bidding_type"
              >
                <Select
                  v-model="form.bidding_type"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder=""
                >
                  <Option v-for="(item,key) in billType" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label=""
                prop="start_bidding_time"
              >
                <DatePicker v-model="form.start_bidding_time" :disabled="isDisabled" type="date" placeholder="" style="width: 100%" transfer clearable></DatePicker>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="proxy_org"
              >
                <Input
                  v-model.trim="form.proxy_org"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label=""
                prop="winning_bid_org"
              >
                <Input
                  v-model.trim="form.winning_bid_org"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label=""
                prop="winning_bid_money"
              >
                <Input
                  v-model.trim="form.winning_bid_money"
                  :disabled="isDisabled"
                  clearable
                  placeholder=""
                  number
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="" prop="remark">
                <Input
                  v-model.trim="form.remark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder=""
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label=""
          name="name7"
        >
          <mulUploadFile ref="uploadFile" :is-disabled="isDisabled" :upload-list="uploadList" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print"></Button>
        <Button icon="md-exit"></Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleCancel('form')"
        >
          
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import mulUploadFile from '@/components/mulUploadFile.vue'
export default {
  name: 'Bidding',
  components: { mulUploadFile },
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: false
    },
    organizationalForm: {
      type: Object,
      default: () => {}
    },
    billType: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      uploadList: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
      },
      //    
      isPop: false,
      //   ->  
      form: {
        id: null,
        bidding_unit: '',
        person_charge: '',
        code: '',
        phone: '',
        bidding_name: '',
        organizational_form: '',
        type: '',
        start_bidding_time: '',
        proxy_org: '',
        winning_bid_org: '',
        winning_bid_money: null,
        remark: ''
      },
      // 
      ruleValidateType: {
        bidding_unit: [
          { required: true, message: '', trigger: 'blur' }
        ],
        person_charge: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        bidding_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        organizational_form: [
          { type: 'number', required: true, message: '', trigger: 'change' }
        ],
        bidding_type: [
          { type: 'number', required: true, message: '', trigger: 'change' }
        ],
        start_bidding_time: [
          { type: 'date', required: true, message: '', trigger: 'blur' }
        ],
        proxy_org: [
          { required: true, message: '', trigger: 'blur' }
        ],
        winning_bid_org: [
          { required: true, message: '', trigger: 'blur' }
        ],
        winning_bid_money: [
          { type: 'number', required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.isPop = false;
    },
    //    
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = that.isCreate
          const method = isCreate ? '/bidding/addBidding' : '/bidding/updateBidding'
          const form = Object.assign({}, that.form)
          form.start_bidding_time = new Date(form.start_bidding_time).getTime()
          form.relationFileIds = this.$refs.uploadFile.getFileIds()
          that.$Spin.show();
          that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 
            that.$Spin.hide();
            if (res.code === 1) {
              that.isPop = false
              that.$Message.success(res.msg);
              that.$emit('refreshList')
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('');
            // 
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('');
        }
      })
    },
    showModal(id) {
      const that = this;
      that.clearForm();
      if (id) {
        // this.$nextTick(() => {
        //   this.$refs.uploadFile.getFileItems()
        // })
        that.$Spin.show();
        that.commonUtil.ajaxGetUtil('/bidding/searchBiddingDetails/' + id, {}, (res) => {
          // 
          that.$Spin.hide();
          if (res.code === 1) {
            const form = res.data
            form.start_bidding_time = new Date(form.start_bidding_time)
            that.form = form
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems(form.relationFileids)
              that.isPop = true
            })
          } else {
            that.$Message.error(res.msg);
          }
        }, () => {
          that.$Message.error('');
          // 
          that.$Spin.hide();
        }, that)
      } else {
        that.isPop = true
      }
    },
    clearForm() {
      this.form = {
        id: null,
        bidding_unit: '',
        person_charge: '',
        phone: '',
        code: '',
        bidding_name: '',
        organizational_form: '',
        type: '',
        start_bidding_time: '',
        proxy_org: '',
        winning_bid_org: '',
        winning_bid_money: null,
        remark: ''
      }
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>



