<template>
  <div>
    <Modal v-model="isPopUp" class="selStyle specialWidth" :mask-closable="false">
      <p slot="header">
        <span></span>
      </p>
      <Form ref="form" :model="form" :label-width="80">
        <Tabs v-model="tabName" :animated="false" class="innerTabStyle">
          <TabPane label="" name="name1">
            <Row>
              <Col span="24">
              <FormItem label="" prop="projectName">
                <Input v-model.trim="form.projectName" disabled></Input>
              </FormItem></Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.carryUnit" disabled></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.capitalType" disabled></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.manageUnit" disabled></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="" prop="implementSite">
                <Input v-model.trim="form.implementSite" disabled></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem label="">
                <Input v-model.trim="form.workContent" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem label="">
                <Input v-model.trim="form.implementJindu" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
              </FormItem>
              </Col>
            </Row>

          </TabPane>
          <TabPane label="" name="name2">
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.allFinancial" disabled number>
                <span slot="append"></span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.centralFinancial" disabled number>
                <span slot="append"></span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.cityFinancial" disabled number>
                <span slot="append"></span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.districtFinancial" disabled number>
                <span slot="append"></span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="">
                <Input v-model.trim="form.elseFinancial" disabled number>
                <span slot="append"></span>
                </Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane label="" name="name3">
            <Col span="24">
            <FormItem label="">
              <Input v-model.trim="form.workTarget" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
            </FormItem>
            </Col>
            <Col span="24">
            <FormItem label="">
              <Input v-model.trim="form.expectBenefit" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
            </FormItem>
            </Col>
          </TabPane>
          <TabPane class="uploadStyle" label="" name="name4">
            <uploadFile ref="uploadFile" is-disabled :form="{fileids: form.projectFileIds}" />
          </TabPane>
        </Tabs>
      </Form>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'InvestSummary',
  components: {
    uploadFile
  },
  props: {
    searchData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      tabName: 'name1',
      form: {
        // 
        projectName: '',
        // 
        implementSite: '',
        // 
        workContent: '',
        // 
        implementJindu: '',
        // 
        allFinancial: '',
        // 
        centralFinancial: '',
        // 
        cityFinancial: '',
        // 
        districtFinancial: '',
        // 
        elseFinancial: '',
        // 
        workTarget: '',
        // 
        expectBenefit: ''
      },
      isPopUp: false
    }
  },
  mounted() {

  },
  methods: {
    search() {
      const that = this;
      that.clearFormData();
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/project/searchProject', that.searchData, (res) => {
        // 
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          if (data.length === 1) {
            that.form = data[0]
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems()
            })
            that.isPopUp = true
          } else {
            that.$Message.error('');
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('');
        // 
        that.$Spin.hide();
      }, that)
    },
    clearFormData() {
      this.form = {
        // 
        projectName: '',
        // 
        implementSite: '',
        // 
        workContent: '',
        // 
        implementJindu: '',
        // 
        allFinancial: '',
        // 
        centralFinancial: '',
        // 
        cityFinancial: '',
        // 
        districtFinancial: '',
        // 
        elseFinancial: '',
        // 
        workTarget: '',
        // 
        expectBenefit: ''
      }
      this.tabName = 'name1'
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>



